/* src/index.css */
:root {
  /* Your Color Palette */
  --primary: #4B352A;       /* Dark brown */
  --accent: #CA7842;        /* Rust orange */
  --secondary: #B2CD9C;     /* Sage green */
  --background: #F0F2BD;    /* Cream */
  
  /* Additional useful variables */
  --text-dark: #4B352A;
  --text-light: #F0F2BD;
  --border-radius: 8px;
  --box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

/* Base Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
  color: var(--text-dark);
  line-height: 1.6;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  color: var(--primary);
  line-height: 1.2;
}

a {
  color: var(--accent);
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--primary);
}

/* Buttons */
button {
  background-color: var(--accent);
  color: var(--text-light);
  border: none;
  padding: 12px 24px;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

button:hover {
  background-color: var(--primary);
  transform: translateY(-2px);
  box-shadow: var(--box-shadow);
}

/* Utility Classes */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  padding: 80px 0;
}

.text-center {
  text-align: center;
}

/* Responsive Base */
@media (max-width: 768px) {
  .section {
    padding: 60px 0;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 40px 0;
  }
}